"use client";

import { useState } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input"
import { Card, CardContent } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Upload, CheckCircle2, XCircle, X } from "lucide-react";
import { observer } from "mobx-react-lite";


const ClaimCard = ({ shopId, verify, loading }) => {
    const [file, setFile] = useState(null);
    const [phone, setPhone] = useState('')
    const [preview, setPreview] = useState(null);
    const [result, setResult] = useState(null);

    const handleFileSelect = () => {
        document.getElementById("hidden-file-input")?.click();
    };

    // image handle 
    const handleFileChange = (e) => {
        const selected = e.target.files?.[0];
        if (!selected) return;

        setFile(selected);
        setPreview(URL.createObjectURL(selected));
        setResult(null);
    };

    // remove selected image 
    const removeImage = () => {
        setFile(null);
        setPreview(null);
    };

    const handleVerify = (e) =>{

        e.preventDefault()
        verify(file, phone)
    }



    return (
        <Card className="w-full shadow-md border rounded-xl overflow-hidden max-w-sm bg-white">
            {/* Header */}
            <div className="bg-linear-to-r from-primary to-secondary text-white text-center py-4">
                <h1 className="text-lg font-semibold">Claim Your Stamp</h1>
                <p className="text-white/80 text-xs">
                    Upload your UPI payment screenshot
                </p>
            </div>

            {/* Content */}
            <CardContent className="flex flex-col gap-5 py-5">

                {/* Upload box */}
                <div
                    onClick={!preview ? handleFileSelect : undefined}
                    className="
            relative
            flex flex-col items-center justify-center
            border-2 border-dashed border-gray-300 rounded-lg
            w-full cursor-pointer transition
            bg-gray-50 hover:bg-gray-100 p-5
            min-h-[170px]">
                    {!preview ? (
                        <div className="flex flex-col items-center gap-2 text-gray-500">
                            <Upload className="w-10 h-10 opacity-70" />
                            <p className="text-sm font-semibold">Tap to upload screenshot</p>
                        </div>
                    ) : (
                        <div className="relative rounded-lg overflow-hidden shadow border">
                            <Image
                                src={preview}
                                alt="preview"
                                width={260}
                                height={260}
                                className="object-cover"
                            />

                            {/* Remove Image Button */}
                            <button
                                onClick={removeImage}
                                className="
                  absolute top-2 right-2 p-1.5 rounded-full
                  bg-black/50 hover:bg-black/70 text-white
                  transition
                "
                            >
                                <X className="w-4 h-4" />
                            </button>
                        </div>
                    )}
                </div>

                {/* Hidden Input */}
                <input
                    id="hidden-file-input"
                    type="file"
                    accept="image/*"
                    onChange={handleFileChange}
                    className="hidden"
                />

                {/* mobile  */}
                <div className="w-full">
                    <label className="block text-sm font-medium text-primary text-left mb-1">Phone</label>
                    <div className="relative">
                        <span className="absolute left-2 top-1/2 -translate-y-1/2 text-gray-500 text-sm">
                            +91
                        </span>

                        <Input
                            type="text"
                            name="phone"
                            placeholder="XXXXX XXXXX"
                            maxLength={10}
                            value={phone}
                            onChange={(e) => { setPhone(e.target.value) }}
                            className="pl-10"
                        />
                    </div>
                </div>

                {/* Button */}
                <Button
                    disabled={!file || loading || !phone.trim() || phone.length < 10}
                    className="w-full py-3 text-md font-medium rounded-lg"
                    onClick={handleVerify}
                >
                    {loading ? "Verifying..." : "Submit"}
                </Button>

                {/* Result alerts */}
                {result?.success && (
                    <Alert className="bg-green-50 border-green-300">
                        <CheckCircle2 className="h-5 w-5 text-green-700" />
                        <AlertTitle className="text-green-700 font-semibold">
                            Payment Verified!
                        </AlertTitle>
                        <AlertDescription className="text-green-600">
                            Stamp added successfully ðŸŽ‰
                        </AlertDescription>
                    </Alert>
                )}

                {result?.error && (
                    <Alert variant="destructive">
                        <XCircle className="h-5 w-5" />
                        <AlertTitle className="font-semibold">Verification Failed</AlertTitle>
                        <AlertDescription>{result.error}</AlertDescription>
                    </Alert>
                )}
            </CardContent>
        </Card>
    );
};

export default observer(ClaimCard);
